import React from 'react';
import {useAppDispatch, useAppSelector} from "../../hooks/hooks";
import {addItem, itemsType} from "../../redux/slices/cartSlice";

type PizzasItemTypeProps = {
    id: number
    title: string
    price: number
    img: string
    sizes: Array<number>
    types: Array<number>
}

const PizzaItem = ({id, title, price, img, sizes, types}: PizzasItemTypeProps) => {
    const dispatch = useAppDispatch()
    const item: itemsType | undefined = useAppSelector(state => state.cart.items.find(item => item.id === id))
    const [type, setType] = React.useState<number>(0)
    const [size, setSize] = React.useState<number>(0)
    const typesNames:Array<string> = ['', '']
    const sizesNames: Array<number> = [26, 30, 40]
    const addPizzasHandler = () => {
        const item: any = {
            id,
            title,
            price,
            img,
            sizes: sizesNames[size],
            types: typesNames[type]
        }
        dispatch(addItem(item))
    }
    return (
        <div className="pizza-block-wrapper">
            <div className="pizza-block">
                <img className="pizza-block__image" src={img} alt="Pizza"/>
                <h4 className="pizza-block__title">{title}</h4>
                <div className="pizza-block__selector">
                    <ul>
                        {types.map((t, i) => <li key={i} className={t === type ? 'active' : ''} onClick={() => setType(t)}>{typesNames[t]}</li>)}
                    </ul>
                    <ul>
                        {sizes.map((s, i) => <li key={i} className={size === i ? 'active' : ''} onClick={() => setSize(i)}>{s} .</li>)}
                    </ul>
                </div>
                <div className="pizza-block__bottom">
                    <div className="pizza-block__price"> {price} </div>
                    <button onClick={addPizzasHandler} className="button button--outline button--add">
                        <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path
                                d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                                fill="white"
                            />
                        </svg>
                        <span></span>
                        {item?.count && <i>{item?.count}</i>}
                    </button>
                </div>
            </div>
        </div>
    );
};

export default PizzaItem;